<template>
  <div>
    <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item to="">用户管理</el-breadcrumb-item>
        <el-breadcrumb-item to="">用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="project-top">
      <el-form :inline="true" class="demo-form-inline" size="mini">
        <el-form-item label="钱包地址:">
          <el-input clearable placeholder="钱包地址" v-model="address"></el-input>
        </el-form-item>
        <el-form-item label="昵称:">
          <el-input clearable placeholder="昵称" v-model="nickname"></el-input>
        </el-form-item>
        <el-form-item label="H2O排序:">
            <el-select clearable v-model="h2oSort" placeholder="请选择" @change="SorfChange(usdtSort = '')">
                <el-option label="从大到小" value="1"></el-option>
                <el-option label="从小到大" value="2"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="USDT排序:">
            <el-select clearable v-model="usdtSort" placeholder="请选择" @change="SorfChange(h2oSort = '')">
                <el-option label="从大到小" value="1"></el-option>
                <el-option label="从小到大" value="2"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="SearchClick()">搜索</el-button>
          <!-- <el-button class="pull-right" type="primary" @click="AddUserInfoShow()">添加NFT</el-button> -->
        </el-form-item>
      </el-form>
      <!-- <el-button class="pull-right" type="primary" @click="AddUserInfoShow()">添加NFT</el-button> -->
      <!-- <el-button @click="AddUser()">添加管理员</el-button> -->
    </div>
    <el-descriptions title="汇总">
        <el-descriptions-item label="H2O总结余">{{ keepDecimalNotRounding(h2oCountBalance, 4) }} H2O</el-descriptions-item>
        <el-descriptions-item label="USDT总结余"> {{ keepDecimalNotRounding(usdtCountBalance, 4) }} USDT</el-descriptions-item>
    </el-descriptions>
    <el-table :data="tableData" style="width: 100%;">
      <el-table-column sortable prop="id" label="ID" width="100" align="center" fixed="left"></el-table-column>
      <el-table-column prop="username" label="用户名" align="center"></el-table-column>
      <el-table-column prop="address" label="Token" align="center" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column prop="nickname" label="用户昵称" align="center" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column label="头像" align="center">
        <template slot-scope="scope">
          <div>
            <!-- <img :src="scope.row.img" alt="log" srcset="" width="50"> -->
              <!-- :preview-src-list="srcList" -->
            <el-image 
              style="width: 50px;cursor:pointer;"
              :src="scope.row.avatar" 
              @click="getPreviewFiles(scope.row)"
              >
            </el-image>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="time" label="注册时间" width="180" align="center"></el-table-column>
      <!-- <el-table-column prop="amount" label="H2O链上余额" width="120" align="center">
        <template slot-scope="scope">
          <span>{{scope.row.wallet_balance}} H2O</span>
        </template>
      </el-table-column>
      <el-table-column prop="amount" label="H2O本地余额" width="120" align="center">
        <template slot-scope="scope">
          <span>{{scope.row.local_balance}} H2O</span>
        </template>
      </el-table-column> -->
      <el-table-column prop="amount" label="H2O平台余额" width="120" align="center">
        <template slot-scope="scope">
          <span>{{ keepDecimalNotRounding(Number(scope.row.wallet_balance) + Number(scope.row.local_balance), 2)}} H2O</span>
        </template>
      </el-table-column>
      <!-- <el-table-column prop="amount" label="USDT链上余额" width="120" align="center">
        <template slot-scope="scope">
          <span>{{scope.row.usdt_wallet_balance}} USDT</span>
        </template>
      </el-table-column>
      <el-table-column prop="amount" label="USDT本地余额" width="120" align="center">
        <template slot-scope="scope">
          <span>{{scope.row.usdt_local_balance}} USDT</span>
        </template>
      </el-table-column> -->
      <el-table-column prop="amount" label="USDT平台余额" width="120" align="center">
        <template slot-scope="scope">
          <span>{{ keepDecimalNotRounding(Number(scope.row.usdt_wallet_balance) + Number(scope.row.usdt_local_balance), 2) }} USDT</span>
        </template>
      </el-table-column>
      <!-- <el-table-column label="操作" width="200" align="center" fixed="right">
        <template slot-scope="scope">
          <div v-if="scope.row.is_del == 1">
            <el-button type="text" @click="rackUpStart(scope.row, 1)" v-if="scope.row.state == 0">上架</el-button>
            <el-button type="text" @click="rackUpStart(scope.row, 0)" v-if="scope.row.state == 1">下架</el-button>
            <el-button type="text" @click="DelData(scope.row)">删除</el-button>
          </div>
          <div v-else>
            <span>无效</span>
          </div>
        </template>
      </el-table-column> -->
    </el-table>
    <el-row class="pages">
      <el-col :span="24">
        <div style="float:right;">
          <wbc-page
            :total="total"
            :pageSize="pageSize"
            :currPage="currPage"
            @changeLimit="limitPaging"
            @changeSkip="skipPaging"
          ></wbc-page>
        </div>
      </el-col>
    </el-row>


    <el-dialog
      title=""
      :visible.sync="previewFilesShow"
      :width="dialogWidth"
      :before-close="previewFilesShowClose"
      class="preview-class">
      <div v-if="fileTempType == 1">
          <el-carousel :interval="5000" :autoplay="false" arrow="always">
            <el-carousel-item v-for="item in imagesUrls" :key="item">
              <!-- <h3>{{ item }}</h3> -->
              <img :src="item" alt="" srcset="">
            </el-carousel-item>
          </el-carousel>
      </div>
      <div v-if="fileTempType == 2">
        <video 
          id="videoPlay"
          ref="player" 
          class="video"
          controls 
          autoplay
          preload="auto"
          :src="videoUrl"
          :poster="videoPoster"
          v-if="videoUrl"
          controlslist="noplaybackrate nodownload nofullscreen noremoteplayback"
          disablePictureInPicture="true"></video>
      </div>
      <!-- <span slot="footer" class="dialog-footer">
        <el-button @click="previewFilesShow = false">取 消</el-button>
        <el-button type="primary" @click="previewFilesShow = false">确 定</el-button>
      </span> -->
    </el-dialog>
  </div>
</template>
<script>
import Page from "@/components/Page.vue";
import { get, post, upload } from "@/common/axios.js";
export default {
  data() {
    return {
      currPage: 1, //当前页
      pageSize: 20, //每页显示条数
      total: 100, //总条数
      PageSearchWhere: [], //分页搜索数组
      nickname: "",
      address: "",
      status: "",
      class_id: "",
      imageUrl: '',
      fileObjData: {},
      tableData: [],
      srcList: [], //列表存放大图路径
      dialogVisibleShow: false,
      DialogTitle: '添加',
      is_save_add_start: 1, //1：添加 2：修改
      is_img_upload: 0, 
      id: '', //球队id
      type: '', //球队类型
      GradeArr: [], //等级数据
      ClassArr: [], //分类数据
      UserAuthUid: 0, //当前登录用户id
      previewFilesShow: false,
      fileTempType: 2,
      dialogWidth: '50%',
      imagesUrls: [],
      videoUrl: '',
      videoPoster: '',
      h2oSort: '',
      usdtSort: '',
      h2oCountBalance: 0,
      usdtCountBalance: 0,
    };
  },
  methods: {
    getListData(ServerWhere) {
      var that = this.$data;
      if (!ServerWhere || ServerWhere == undefined || ServerWhere.length <= 0) {
        ServerWhere = {
          limit: that.pageSize,
          page: that.currPage,
        };
      }
      get("/Admin/User/getUserList", ServerWhere, json => {
          console.log(json);
        if (json.data.code == 10000) {
          this.tableData = json.data.data.data;
          this.total = json.data.data.count;
          this.h2oCountBalance = json.data.data.h2o_count_balance;
          this.usdtCountBalance = json.data.data.usdt_count_balance;
          this.tableData.forEach((item) => { //存放大图
            this.srcList.push(item.cover);
          });
        } else {
          this.$message.error("加载数据失败");
        }
      });
    },
    // getClassList() {
    //   get("/H2omarketplace/H2onft/getClassList", {}, (json) => {
    //       // console.log(json);
    //     if (json.data.code == 10000) {
    //       this.ClassArr = json.data.data;
    //     } else {
    //       this.$message.error("加载数据失败");
    //     }
    //   });
    // },
    SearchClick() {
      //搜索事件
      var SearchWhere = {
        page: this.currPage,
        limit: this.pageSize,
      };
      if (this.nickname && this.nickname !== "") {
        SearchWhere["nickname"] = this.nickname;
      }
      if (this.address && this.address !== "") {
        SearchWhere["address"] = this.address;
      }
      if (this.h2oSort && this.h2oSort !== "") {
        SearchWhere["h2oSort"] = this.h2oSort;
      }
      if (this.usdtSort && this.usdtSort !== "") {
        SearchWhere["usdtSort"] = this.usdtSort;
      }
      this.PageSearchWhere = [];
      this.PageSearchWhere = SearchWhere;
      this.getListData(SearchWhere);
    },
    SearchReset() {
      //搜索条件重置
      this.PageSearchWhere = [];
      this.getListData();
    },
    limitPaging(limit) {
      //赋值当前条数
      this.pageSize = limit;
      if (
        this.PageSearchWhere.limit &&
        this.PageSearchWhere.limit !== undefined
      ) {
        this.PageSearchWhere.limit = limit;
      }
      this.getListData(this.PageSearchWhere); //刷新列表
    },
    skipPaging(page) {
      //赋值当前页数
      this.currPage = page;
      if (
        this.PageSearchWhere.page &&
        this.PageSearchWhere.page !== undefined
      ) {
        this.PageSearchWhere.page = page;
      }
      this.getListData(this.PageSearchWhere); //刷新列表
    },
    DelData(row) { //删除管理员
        this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
            get('/Admin/Notes/delNotesRow', {id: row.id}, (json) => {
                if (json && json.data.code == 10000) {
                    this.getListData();
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                } else if(json.data.code == 10007) {
                  this.$message.error(json.data.msg);
                } else {
                    this.$message.error(json.data.msg);
                }
            })
        }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });
        });
    },
    resetForm(formName) {
      // console.log(this.$refs[formName])
      this.$refs[formName].resetFields();
      this.dialogVisibleShow = false;
    },
    rackUpStart(row, status) { //审批 修改状态
        get('/Admin/Notes/rackUpStart', {id: row.id, status: status}, (json) => {
            if(json.data.code == 10000) {
                this.$message({
                    message: '操作成功',
                    type: 'success'
                });
                this.getListData();
            } else if(json.data.code == 10007) {
                this.$message.error(json.data.msg);
            } else {
                this.$message.error('修改失败');
            }
        })
    },
    previewFilesShowClose() {
      if(this.fileTempType == 2) {
        this.$refs.player.pause();//暂停
      }
      this.previewFilesShow = false;
    },
    getPreviewFiles(row) { //预览
        this.imagesUrls = row.avatar;
        console.log(row);
    }

  },
  created() {
    this.getListData();
    this.UserAuthUid = localStorage.getItem("UserAuthUid");
  },
  components: {
    "wbc-page": Page, //加载分页组件
  }
};
</script>
<style lang="scss" scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    /* width: 178px; */
    width: 100%;
    height: 230px;
    display: block;
  }
  .el-radio-group .el-radio + .el-radio {
    margin-left: 0 !important;
  }
  .pages {
    margin-top: 0!important;
    margin-bottom: 80px !important;
  }
  .el-breadcrumb {
    z-index: 10 !important;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  /deep/ {
    .preview-class {
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
      video {
        // position: absolute;
        width: 100%;
        height: 70vh;
      }
      .el-dialog__headerbtn {
        z-index: 1000;
        color: rgb(3, 3, 3);
        .el-dialog__close {
          color: rgb(3, 3, 3);
          font-size: 20px;
          font-weight: 900;
        }
      }
      .el-dialog__header {
        padding: 0 !important;
      }
      .el-dialog__body {
        padding: 0;
      }
      /* height: 100%; */
    }
  }
</style>



